@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600;800&family=Nunito:wght@900&display=swap');
@import './normalize.css';

:root {
  --primary-color: #fff068;
  --secondary-color: #fd7898;
  --base-color: #323031;
  --success-color: #62ed89;
  --error-color: #ff5656;
  --blue-color: #378beb;
  --red-color: #f53b3b;
  --green-color: #3dee6f;
  --yellow-color: #fcea48;
  --purple-color: #b66df0;
  --black-color: #000000;
  --white-color: #ffffff;
  --grey-color: #b4b4b4;
  --grey-dark-color: #909090;
  --grey-light-color: #f6f6f6;
}

* {
  box-sizing: border-box;
}

*::selection {
  background-color: var(--primary-color);
  color: var(--base-color);
}

html {
  font-size: 10px;
  line-height: 1;
}

body {
  color: var(--base-color);
  font-family: 'Nunito Sans', Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4 {
  font-family: Nunito, Arial, sans-serif;
  font-weight: 900;
  letter-spacing: -2px;
  line-height: 1;
  margin: 0;
}

h1 {
  font-size: 64px;
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 32px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}
